body {
    font-family: "Raleway", sans-serif;
    text-align: center;
    min-width: 600px
  }
  
h1 {
  font-size: 75px;
}

h2 {
  font-size: 20px;
  padding-bottom: 20px;
  font-style: italic;
}

ul {
  list-style: none;
  line-height: 36px;
}

button {
  font-family: "Raleway", sans-serif;
  font-size: 24px;
  padding: 20px;
  margin: 20px auto;
  width: 300px;
  display: block;
}

.game {
  font-size: 16px;
  width: 150px;
  margin: 20px 20px;
  display: inline;
}

img {
  max-width: 100%;
  max-height: 100%;
}

.footer {
  position: absolute;
  bottom: 0;
}

text {
  font-size: 12px;
}

#container {
  position: relative;
  margin: auto;
  height: 200px;
  width: 620px;
  border: 1px solid black;
}

#person {
  position:absolute;
  padding: 8px;
  height: 50px;
  width: 50px;

}

#animate {
  position: absolute;
  height: 50px;
  width: 50px;
  left: 570px;
}


